import { useRef, useState } from 'react'
import { useSpring, animated, interpolate } from 'react-spring'
import theme from '../../theme'

const StandardInput = ({ placeholder = 'Name' }) => {
  const [isSpring, setIsSpring] = useState(false)
  const { o, color } = useSpring({
    from: { o: 0.8, color: theme.palette.shade[30] },
    o: isSpring ? 1 : 0.8,
    color: isSpring ? theme.palette.blue : theme.palette.shade[30]
  })
  const inputStyle = {
    border: interpolate([o, color], (o, c) => `${o * 1}px solid ${c}`),
    outline: 'none',
    borderRadius: '4px',
    padding: `${theme.spacing}px ${theme.spacing * 1.5}px`
  }
  const titleStyle = {
    position: 'absolute',
    top: '0px',
    font: theme.font.size.small
  }
  return (
    <>
      <div id="standard-input">
        <animated.span style={titleStyle}>{placeholder}</animated.span>
        <animated.input
          type="text"
          style={inputStyle}
          onFocus={() => setIsSpring(true)}
          onBlur={() => setIsSpring(false)}
        />
      </div>
      <style jsx>{`
         {
        }
      `}</style>
    </>
  )
}

export default StandardInput
